.button {
    border: 1px solid black;
    background-color: var(--primaryColor, #0fa3b1);
    border-radius: 10px;
    padding: 1em;
    margin: 15px auto;
    width: 100%;
    max-width: 100%;
    font-size: 18px;
    transition: 0.25s;
    display: block;
  }
  
  .button:hover {
    background-color: var(--accentOne, #f9dc66);
    cursor: pointer;
  }
  
  .button:active {
    background-color: var(--accentTwo, #ff9b42);
  }
  